.card {
  box-shadow: 2px 2px 2px rgba(255, 255, 255, 0.37);
  width: calc(18% - 16px);
  max-height: 300px;
  position: relative;
  margin: 0 8px;
  margin-bottom: 15px;
  box-sizing: border-box;
}
.imgC {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.imgC > img {
  width: 100%;
  height: 100%;
}
.infoC {
  opacity: 0;
  background-color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  transition: opacity 1s;
}
.infoCC {
  display: block;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  opacity: 0.7;
  background-color: black;
  box-shadow: 0 0 5px snow, 2px 2px 1px goldenrod;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1%;
  z-index: 10;
  transition: opacity 700ms;
}
.infoCC > p {
  letter-spacing: 3px;
  color: rgba(255, 255, 255, 0.726);
  text-align: center;
  text-transform: capitalize;
  margin-top: 0;
  margin-bottom: 0;
  padding: 2%;
  border-bottom: 1px solid rgba(218, 165, 32, 0.342);
  border-radius: 5px;
  z-index: 10;
}

@media (max-width: 900px) {
  .card {
    width: calc(25% - 16px);
  }
}
@media (max-width: 768px) {
  .card {
    width: calc(33.3% - 16px);
  }
}
@media (max-width: 582px) {
  .card {
    width: calc(50% - 16px);
  }
}

@media (max-width: 370px) {
  .card {
    width: calc(100% - 16px);
    margin-top: 11%;
  }
  
}
